import { Observable, startWith, Subscription } from 'rxjs';
import { CommonModule } from '@angular/common';
import { ChangeDetectionStrategy, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { TranslateModule, TranslateService } from '@ngx-translate/core';

import { IPostFull, Post } from '@ApiClient/backend/Post/post';
import { IPostService } from '@ApiClient/services/post';
import { ToService } from '@ApiClient/helpers/observables';

import { ImageComponent } from '@ApiClient/components/image/image.component';
import { URLS } from '@enums/urls';
import { RouterLink } from '@angular/router';
import { map } from 'rxjs/operators';
import { ClickedOutsideModule } from '../../directives/clickedOutside.module';
import { PostHeaderComponent } from '@components/post-header/post-header.component';
import { PostFooterComponent } from '@components/post-footer/post-footer.component';
import { selectCurrentUserId } from '@store/auth/auth.selectors';
import { Store } from '@ngrx/store';
import { AppStateInterface } from '@store/app/app-state.interface';

@Component({
  selector: 'app-post-card',
  standalone: true,
  imports: [CommonModule, TranslateModule, ImageComponent, RouterLink, ClickedOutsideModule, PostHeaderComponent, PostFooterComponent],
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './post-card.component.html',
  styleUrl: './post-card.component.scss',
})
export class PostCardComponent implements OnInit, OnDestroy {
  @Input() post!: IPostFull;
  urlThumbnail$!: Observable<string>;
  postService: IPostService;
  currentUserSubscription: Subscription = new Subscription();
  isCurrentUserPost: boolean = false;
  currentUserId$: Observable<string | null> = this.store.select(selectCurrentUserId);
  locale$: Observable<string> = this.translate.onLangChange.pipe(
    startWith({ lang: this.translate.currentLang }),
    map((event) => event.lang),
  );

  constructor(
    private translate: TranslateService,
    public store: Store<AppStateInterface>,
  ) {
    this.postService = ToService<IPostService>(Post);
  }

  ngOnInit() {
    this.setUpImage();
    this.currentUserSubscription = this.currentUserId$.subscribe((currentUserId) => {
      if (currentUserId === this.post.userCreatorId) {
        this.isCurrentUserPost = true;
      }
    });
  }

  ngOnDestroy() {
    this.currentUserSubscription.unsubscribe();
  }

  setUpImage() {
    this.urlThumbnail$ = this.postService.getUrlPostFileThumbnail({
      fileId: this.post?.postFiles[0].fileId,
      userCreatorId: this.post?.userCreatorId,
      id: this.post?.postFiles[0].id,
    });
  }

  protected readonly URLS = URLS;
}
