import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { RouterLink } from '@angular/router';
import { DialogModule } from 'primeng/dialog';
import { ConfirmDialogModule } from 'primeng/confirmdialog';
import { RippleModule } from 'primeng/ripple';
import { DialogService, DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';
import { Store } from '@ngrx/store';
import { AppStateInterface } from '@store/app/app-state.interface';
import { deletePostAction } from '@store/post/post.action';

@Component({
  selector: 'app-post-delete-modal',
  templateUrl: 'post-delete-modal.component.html',
  styleUrls: ['post-delete-modal.component.scss'],
  standalone: true,
  imports: [CommonModule, TranslateModule, RouterLink, DialogModule, ConfirmDialogModule, RippleModule],
  providers: [DialogService],
})
export class PostDeleteModalComponent {
  constructor(
    private ref: DynamicDialogRef,
    private config: DynamicDialogConfig,
    private store: Store<AppStateInterface>,
  ) {}

  closeModal() {
    this.ref.close();
  }

  deletePost() {
    this.store.dispatch(deletePostAction({ postId: this.config.data.postId, userId: this.config.data.userId }));
    this.ref.close();
  }
}
